.gallery__wrapper {
  display: flex;
  flex-wrap: wrap;
  padding-top: 20px;
  padding-bottom: 40px;
}

.gallery__list {
  margin: 20px;
  transform: scale(1);
  transition: transform 150ms linear;

 
}

.container-medium {
  width: 300px;
  padding: 10px;
  box-shadow: 10px 10px rgb(240, 240, 240);
  transition: transform 0.5s ease;

  background: rgb(40, 153, 208);
  background: linear-gradient(
  90deg,
  rgba(40, 153, 208, 1) 33%,
  rgba(0, 125, 232, 1) 74%,
  rgba(148, 217, 233, 1) 100%
);

color: white;
border-radius: 5px;

}
.container-medium:hover {
  transform: scale(1.025);
  cursor: grab;
}

.main {
  display: flex;
  justify-content: center;
  align-items: center;
}
.centered_cont {
  position: relative;
  /* display: flex;
  justify-content: space-between;
  align-items: center; */
}
.btn {
  position: absolute;
  top: 0;
  right: 0;
  margin: 5px;
  border-radius: 10px;
  /* border: none; */
  background-color: transparent;
  cursor: pointer;
}

.wraper {
  display: flex;
  /* justify-content: center; */
  align-items: center;
}

.fav-info {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
}

.svg__plus {
  fill: black;
}


.update{
position: absolute;
bottom:  0;
right: 0;
margin: 5px;
border-radius: 10px;
/* border: none; */
background-color: transparent;
cursor: pointer;


}


